<template>
 <div id="newsInfo">
     <!-- 标题 -->
     <h2> {{news.title}}</h2>
     <!-- 子标题 -->
     <p>
         <span>创建时间：{{news.add_time | dateFormat}}</span>
         <span>点击：{{news.click}}次</span>
     </p>
     <hr>
     <!-- 内容 -->
     <div class="content" v-html="news.content"></div>

      <!-- 加载评论组件 -->
      <!-- 父子组件传值 -->
    <my-comment :id="this.id"></my-comment>
 </div>
</template>

<script>
// 引入评论控件
import comment from '../subComponents/Comment'
export default {
 data() {
 return {
     id : this.$route.params.id,
     news:{}
 }
 },
 methods: {
     getNew(){
         
         this.$http.get('api/getnew/'+this.id).then(response => {
             console.log(response.body);
             this.news = response.body.message[0]
         },err => {

         })
     }
 }
 ,mounted(){
     this.getNew()
 },components:{
     'my-comment':comment
 }
}
</script>

<style lang="scss" scoped >
    #newsInfo{
        h2{
            font-size: 16px;
            text-align: center;
            margin: 15px 0;
            color:red;
        }
        p{
            font-size: 13px;
            display: flex;
            justify-content:space-between;
        }

    }

</style>



