<template>
 <div id="photoInfo">
    <h3>{{ photoInfo.title }}</h3>
    <p class="subtitle">
        <span>添加时间：{{ photoInfo.add_time | dateFormat }}</span>
        <span>点击次数：{{ photoInfo.click }}</span>
    </p>
    <hr>

    <!-- 缩略图区域 -->
    <vue-preview
      :list="list"
      :thumbImageStyle="{width: '80px', height: '80px', margin: '10px'}"
      :previewBoxStyle="{border: '1px solid #eee'}"
      :tapToClose="true"
      @close="closeHandler"
      @destroy="destroyHandler"
    />

    <!-- 图片内容区域 -->
    <div class="content" v-html="photoInfo.content"></div>

    <!-- 放置一个现成的评论子组件 -->
    <cmt-box :id="id"></cmt-box>
 </div>
</template>

<script>

//引入评论组件
import comment from '../subComponents/Comment.vue'

export default {
 data() {
 return {
     id:this.$route.params.id,
     list:[],
     photoInfo:{}

 }
 },
 created(){
     this.getImageInfo();
     this.getThumbs();
 }
 , methods: {
     getImageInfo(){
            //获取图片详情
            this.$http.get('api/getimageinfo/'+this.id).then( res => {
                if(res.body.status === 0){  
                    this.photoInfo = res.body.message[0];
                }
            }, err =>{

            })

     },
      getThumbs(){
            //获取图片详情
            this.$http.get('api/getthumimages/'+this.id).then( res => {
                if(res.body.status === 0){
                    res.body.message.forEach(element => {
                        element.width = 600;
                        element.height = 400;
                    });
                    this.list = res.body.message;
                   
                }
            }, err =>{

            })

     },
     // 即将关闭的时候，调用这个处理函数
    closeHandler() {
      console.log('closeHandler')
    },
    // 完全关闭之后，调用这个函数清理资源
    destroyHandler() {
      console.log('destroyHandler')
    }

 },
 components: {
     'cmt-box':comment
 }
}
</script>

<style lang="scss" scoped>
    #photoInfo{
        margin:3px;
        h3{
            color:blue;
            font-size: 15px;
            text-align: center;
            margin: 15px 0;
        }
        .subtitle{
            display: flex;
            justify-content:space-between;
            font-size: 12px;
        }
        .content{
            font-size:13px;
            line-height: 20px;
        }
    }

</style>





