<template>
 <div id="comment">
    <h3>发表评论</h3>
    <hr>
    <textarea placeholder="请输入要评论的内容（最多吐槽120字）" maxlength="120"></textarea>
    <mt-button type="primary" size="large">发表评论</mt-button>
    <!-- 评论内容 -->
    <div class="cmt-list">
        <div class="cmt-item">
            <div class="cmt-title">
                第1楼&nbsp;&nbsp;用户：匿名用户&nbsp;&nbsp;发表时间：2019年4月6日21:17:46
            </div>
            <div class="cmt-body">
                锄禾日当午
            </div>
        </div>
        <div class="cmt-item">
            <div class="cmt-title">
                第1楼&nbsp;&nbsp;用户：匿名用户&nbsp;&nbsp;发表时间：2019年4月6日21:17:46
            </div>
            <div class="cmt-body">
                锄禾日当午
            </div>
        </div>
        <div class="cmt-item">
            <div class="cmt-title">
                第1楼&nbsp;&nbsp;用户：匿名用户&nbsp;&nbsp;发表时间：2019年4月6日21:17:46
            </div>
            <div class="cmt-body">
                锄禾日当午
            </div>
        </div>

    </div>
    <mt-button type="danger" plain size="large">加载更多</mt-button>

 </div>
</template>

<script>
import  {Toast} from 'mint-ui';
export default {
 data() {
 return {
     pageIndex:1, //默认第一页
     list:[]      //所有的评论数据
 }
 },
 methods: {
     getComment(){
         var url = 'api/getcomments/'+this.id+'?pageIndex=' + this.pageIndex;
         this.$http.post(url).then(response => {
             if(response.body.status === 0){
                 this.list = response.body.message;
             }else{
                 Toast("加载评论失败");
             }
         },err => {

         })
     }
 },mounted(){
     this.getComment();
 },props:['id']
}
</script>

<style lang="scss" scoped>
    #comment{
        h3{
            font-size: 18px;
        }
        textarea{
            font-size: 14px;
            margin:0;
            height: 85px;
        }

        .cmt-list{
            margin: 5px 0;
            .cmt-item{
                font-size: 13px;
                .cmt-title{
                    line-height: 30px;
                    background-color: #ccc;
                }
                .cmt-body{
                    line-height: 35px;
                    // 缩进
                    text-indent: 2em;
                }
            }
        }
    }
</style>




