<template>
 <div class="photoList">
     <!-- 顶部滑动区域 -->
     <div id="slider" class="mui-slider ">
				<div id="sliderSegmentedControl" class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
					<div class="mui-scroll">
                        <!-- 类的动态高亮 -->
						<a :class="['mui-control-item',item.id == 0 ? 'mui-active':'']" v-for="(item,key) in categorylist" :key="item.id" @click="getPhotoListByCategoryId(key)" >
							{{item.title}}
						</a>
					

					</div>
				</div>

	 </div>
     <ul class="listItem">
        <router-link v-for="item in photoList" :key="item.id" tag="li" :to="'/home/photoInfo/' + item.id">
            <img v-lazy="item.img_url">
            <div class="info">
                <h1 class="info-title">{{ item.title }}</h1>
                <div class="info-body">{{ item.zhaiyao }}</div>
            </div>
        </router-link >
     </ul>

 </div>
</template>

<script>
import mui from '../../lib/mui/js/mui.min.js'

export default {
 data() {
 return {
     categorylist:[],
     photoList:[]


 }
 },
 created(){
     this.getCategory();
     //默认进入页面请求全部图片
     this.getPhotoListByCategoryId(0);
 }
 ,
 methods: {
     getCategory(){
         this.$http.get('api/getimgcategory').then(result => {
             if(result.body.status === 0){
                 this.categorylist = result.body.message;
                 this.categorylist.unshift({title:'全部',id:0})
             }
         })
     },
     getPhotoListByCategoryId(catId){
         this.$http.get('api/getimages/' + catId).then(result => {
             if(result.body.status === 0){
                 this.photoList = result.body.message;
             }
         })
     }

 },
 components: {

 },mounted(){
     mui('.mui-scroll-wrapper').scroll({
            deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
        });
 }
}
</script>

<style lang="scss" scoped>
    *{
        touch-action: pan-y;
    }

    .photoList{
        .listItem{
            list-style: none;
            margin: 0;
            padding: 10px;
            padding-bottom: 0px;
            li{
                background-color: #ccc;
                text-align: center;
                margin-bottom: 10px;
                position: relative;
                // 边框阴影
                box-shadow: 0 0 6px #999;
                img{
                    width: 100%;
                    vertical-align: middle;
                }
                img[lazy=loading] {
                    width: 40px;
                    height: 300px;
                    margin: auto;
                    }
                .info{
                    position: absolute;
                    bottom: 0;
                    color:white;
                    text-align: left;
                    max-height: 85px;
                    background-color: rgba(0,0,0,0.4);
                    .info-title{
                        font-size: 14px;
                    }
                    .info-body{
                        font-size: 13px;
                    }
                }    

            }

        }
           
        
    }

</style>




